import { useMutation, useQueryClient } from '@tanstack/react-query';
import toast from 'react-hot-toast';
import api from '@/lib/axios';
import type { StoreResponse, UpdateStoreInput } from '../types';

export function useUpdateStoreMutation(storeSlug: string) {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: async (input: UpdateStoreInput): Promise<StoreResponse> => {
      const response = await api.put(`/stores/${storeSlug}/`, input);
      return response.data;
    },
    onSuccess: (data) => {
      queryClient.invalidateQueries({ queryKey: ['stores'] });
      queryClient.invalidateQueries({ queryKey: ['store', storeSlug] });
      queryClient.invalidateQueries({ queryKey: ['store', data.store.slug] });
      toast.success('Store updated successfully');
    },
    onError: (error: Error) => {
      toast.error(error.message || 'Failed to update store');
    },
  });
}

